<template>
	<div class="product-buy-wrapper">
		<div class="product-buy-header">
			<div class="header-left">
				<span class="solution-top"></span>
				<img
					src="../../assets/images/back.png"
					class="back"
					title=""
					@click="back"
				/>
			</div>
			<div class="header-right"></div>
		</div>
		<div class="product-buy-content">
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<img
						:src="
							product_logo || require('../../assets/images/product_logo.png')
						"
						class="product-logo"
					/>
					<div class="product-table">
						<el-table :data="tableData" style="width: 100%">
							<el-table-column label="" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.chName">
										{{ scope.row.chName }}
									</div>
								</template>
							</el-table-column>
							<el-table-column label="" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.enName">
										{{ scope.row.enName }}
									</div>
								</template>
							</el-table-column>
							<el-table-column label="" align="center" width="100">
								<template slot-scope="scope">
									<div class="cut-area">*{{ scope.row.product_num }}</div>
								</template>
							</el-table-column>
							<el-table-column label="" align="center">
								<template slot-scope="scope">
									<div class="cut-area" :title="scope.row.price">
										<span class="original-price">{{ (+scope.row.originalPrice).toFixed(2) }}</span>
										<span class="now-price">{{ (+scope.row.actualPrice).toFixed(2) }}</span>
										<!-- <span></span> -->
									</div>
								</template>
							</el-table-column>
						</el-table>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="flex-center">
						<div class="info-wrapper">
							<div class="active-num">
								<div class="input-title"></div>
								<el-input v-model="active_nums">
									<!-- <template slot="suffix"> </template> -->
									<template slot="append">
										<div class="active-btn">
											<i
												class="el-icon-caret-top activeIcon"
												@click="activeUp"
											></i>
											<i
												class="el-icon-caret-bottom activeIcon"
												@click="activeDown"
											></i>
										</div>
									</template>
								</el-input>
							</div>
							<div class="sn-input">
								<div class="input-title">SN</div>
								<div class="input-area-wrapper">
									<div
										class="input-area"
										v-for="(item, index) in sn_arr"
										:key="index"
									>
										<el-input v-model="item.value"></el-input>

										<img
											src="../../assets/images/problem.png"
											class="problem"
										/>
										<span class="problem-value"
											>{{
												tableData[0].chName
											}}SNXXXXXXXXXXXX</span
										>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content payWay">
					<el-table
						:data="pay_arr"
						style="width: 100%"
						@row-click="changePayWay"
					>
						<el-table-column label="" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.name">
									<el-radio v-model="pay_way" :label="scope.row.name">
										<img
											v-if="scope.row.type === 'icon'"
											:src="scope.row.url"
											class="pay-icon"
											:class="{ card: scope.row.name == '' }"
										/>
										<span class="pay-name">{{ scope.row.name }}</span>
									</el-radio>
								</div>
							</template>
						</el-table-column>
						<el-table-column label="" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.description">
									{{ scope.row.description }}
								</div>
							</template>
						</el-table-column>
						<el-table-column label="" align="center">
							<template slot-scope="scope">
								<div class="cut-area" :title="scope.row.service_charge">
									{{ scope.row.service_charge }}
								</div>
							</template>
						</el-table-column>
					</el-table>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="flex-start">
						<div class="request-bill"></div>
						<el-checkbox v-model="is_invoice"></el-checkbox>
						<span class="invoice-item"></span>
						<el-select v-model="invoice_type" placeholder="">
							<el-option
								v-for="item in options"
								:key="item.value"
								:label="item.label"
								:value="item.value"
							>
							</el-option>
						</el-select>
						<span class="invoice-item"></span>
						<el-input></el-input>
						<span class="invoice-item"></span>
						<el-input></el-input>
					</div>
				</div>
			</div>
			<div class="product-info-wrapper">
				<div class="product-info-header">
					<div class="product-info-title">
						
					</div>
					<div class="split-line"></div>
				</div>
				<div class="product-info-content">
					<div class="total-price-content">
						<div class="divide-area">
							<div class="total-price"> {{ totalPrice }}</div>
							<div class="real-price-wrapper">
								<span class="total-price"></span>
								<span class="real-price"
									> {{ pay_way == "" ? '0.00' : totalPrice }}</span
								>
							</div>
						</div>
						<div class="divide-area space-area">
							<div>
								<el-checkbox v-model="is_agree"></el-checkbox>
								<span class="buy-agreement" @click="TemasekDialog = true"
									></span
								>
								<!-- <span style="font-weight: bold;"></span>
								<span></span> -->
							</div>
							<div class="buy-btn" @click="buyProduct"></div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<my-dialog
			:dialogVisible="dialogVisible"
			title=""
			@closeDialog="dialogVisible = false"
		>
			<div class="input-area">
				<span class="input-title"></span>
				<el-input v-model="card_num" placeholder=""></el-input>
			</div>
			<div class="btn-area">
				<button class="square-btn " @click="dialogVisible = false"></button>
				<button class="square-btn primary split-area" @click="buyProductByCard">
					
				</button>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="payInfoVisible"
			title=""
			@closeDialog="payInfoVisible = false"
			:width="800"
		>
			<div class="payInfo-wrapper">
				<img src="../../assets/images/pay_success.png" />
				<div class="flex-area-wrapper">
					<div class="flex-area">
						<div class="title"></div>
						<div class="value">{{ buyFinishInfo.productName }}</div>
					</div>
					<div class="flex-area">
						<div class="title">SN</div>
						<div class="value">{{ buyFinishInfo.snCode }}</div>
					</div>
					<div class="flex-area">
						<div class="title"></div>
						<div class="value">
							<p id="copy">{{ buyFinishInfo.activeCode }}</p>
						</div>
					</div>
				</div>
			</div>
			<div class="btn-area">
				<button
					class="square-btn primary"
					@click="$router.push('/productList')"
				>
					
				</button>
				<button
					style="margin-left: 50px;"
					class="square-btn copy"
					data-clipboard-action="copy"
					data-clipboard-target="#copy"
					@click="copy"
				>
					
				</button>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="wxDialog"
			title=""
			@closeDialog="beforeAliDialog"
			:width="800"
		>
			<div class="wxPay-head">
				<img src="../../assets/images/aliPayIcon2.png" class="wxPay-icon" />
				<span class="icon-text"></span>
			</div>
			<div class="wxPay-wrapper">
				<div ref="wxPay"></div>
				<div class="wxPay-text">
					<img src="../../assets/images/scan.png" class="scan-icon" />
					<div>
						<p>""</p>
						<p></p>
					</div>
				</div>
				<div class="wxPay-price">{{ totalPrice }}</div>
			</div>
		</my-dialog>
		<my-dialog
			:dialogVisible="TemasekDialog"
			title=""
			@closeDialog="TemasekDialog = false"
			:width="1000"
		>
			<div class="temasekWrapper">
				<p>
					
				</p>
				<h2>1.</h2>
				<p>
					
				</p>
				<h2>2.</h2>
				<p>
					
				</p>
				<h2>3.</h2>
				<p></p>
				<h2>4.</h2>
				<p>
					
				</p>
				<h2>5.</h2>
				<p>
					
				</p>
				<h2>6.</h2>
				<p>
					
				</p>
			</div>
		</my-dialog>
	</div>
</template>

<script>
import myDialog from "../../components/my_dialog";
import { getProductDetail } from "@/api/product";
import { baseUrl } from "../../../config";
import {
	cardPay,
	getInfoByOrderNo,
	getOrderId,
	requestWxPay,
	getOrderStatus,
	aliPay,
	getAliOrderStatus,
} from "@/api/pay";
import Clipboard from "clipboard";
import QRCode from "qrcodejs2";
export default {
	data() {
		return {
			product_logo: "",
			tableData: [
				{ product_num: 1, chName: "", enName: "", introduction: "", originalPrice: "", actualPrice: "" },
			],
			pay_arr: [
				{
					name: "",
					type: "icon",
					url: require("../../assets/images/aliPayIcon.png"),
					description: "",
					service_charge: "00.00",
				},
				// {
				// 	name: "",
				// 	type: "icon",
				// 	url: require("../../assets/images/wxPayIcon.png"),
				// 	description: "",
				// 	service_charge: "00.00",
				// },
				{
					name: "",
					type: "icon",
					url: require("../../assets/images/cardPayIcon.png"),
					description: "",
					service_charge: "00.00",
				},
			],
			pay_way: "",
			active_nums: 1,
			sn_arr: [
				{
					value: "",
				},
			],
			is_invoice: false,
			options: [],
			invoice_type: "",
			is_agree: false,
			dialogVisible: false,
			card_num: "",
			payInfoVisible: false,
			baseUrl: "",
			buyFinishInfo: {
				productName: "",
				snCode: "",
				activeCode: "jafjklajdfjasjdfklj",
			},
			wxDialog: false,
			TemasekDialog: false,
			aliPayTimer: null
		};
	},
	created() {},
	mounted() {
		this.baseUrl = baseUrl;
		this.init();
	},
	components: {
		myDialog,
	},
	computed: {
		totalPrice() {
			return (this.tableData[0].product_num * this.tableData[0].actualPrice).toFixed(2);
		},
	},
	watch: {
		active_nums(newVal, oldVal) {
			newVal = newVal.replace(/[^\d]/g, "");
			if (parseInt(newVal) > 20) {
				newVal = 20;
				this.$message({
					type: "warning",
					message: "20",
				});
			}
			if (+newVal > +oldVal) {
				for (let i = oldVal; i < newVal; i++) {
					this.sn_arr.push({
						value: "",
					});
				}
			} else {
				this.sn_arr.splice(newVal);
			}
			this.active_nums = "" + newVal;
			this.tableData[0].product_num = newVal;
		},
	},
	methods: {
		// +1
		activeUp() {
			let tempNum = parseInt(this.active_nums) + 1;
			this.active_nums = String(tempNum);
		},
		// -1
		activeDown() {
			let tempNum = parseInt(this.active_nums) - 1;
			tempNum = tempNum < 1 ? 1 : tempNum;
			this.active_nums = String(tempNum);
		},
		// 
		copy() {
			var clipboard = new Clipboard(".copy");
			clipboard.on("success", (e) => {
				this.$message({
					message: "",
					type: "success",
				});
				// 
				clipboard.destroy();
			});
			clipboard.on("error", (e) => {
				// 
				clipboard.destroy();
				this.$message({
					message: "",
					type: "fail",
				});
			});
		},
		// 
		beforeAliDialog() {
			this.wxDialog = false;
		},
		// 
		buyProduct() {
			clearInterval(this.aliPayTimer);
			if (!this.is_agree) {
				this.$message({
					type: "warning",
					message: "",
				});
				return;
			}
			if (this.active_nums == "" || this.active_nums == "0") {
				this.$message({
					type: "warning",
					message: "",
				});
				return;
			}

			for (let i = 0; i < this.sn_arr.length; i++) {
				if (this.sn_arr[i].value.replace(/\s+/g, "") == "") {
					this.$message({
						type: "warning",
						message: "SN",
					});
					return;
				}
				if (!/^[A-Fa-f\d]{12}$/.test(this.sn_arr[i].value)) {
					this.$message({
						type: "warning",
						message: "SN",
					});
					return;
				}
			}
			let sns = this.sn_arr.map((item) => {
				return item.value;
			});
			let tempSns = [...new Set(sns)];
			if (tempSns.length !== sns.length) {
				this.$message({
					type: "warning",
					message: "SN",
				});
				return;
			}
			if (this.pay_way == "") {
				let sns = this.sn_arr.map((item) => {
					return item.value;
				});
				let count = +this.active_nums
				let loading = this.$loading({
					lock: true,
					text: '....',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
					});
				aliPay({
					count,
					productSeriesId: this.$route.params.id,
					sns,
					actualPrice: this.totalPrice,
				}).then((res) => {
					// console.log(res);
					loading.close();
					if(res.data.code == "401") {
						this.$message({
							type: "warning",
							message: ""
						})
						this.$router.push("/")
						return;
					}
					if(res.data.code == "400") {
						this.$message({
							type: "warning",
							message: res.data.data
						})
						return;
					}
					this.wxDialog = true;
					this.$nextTick(() => {
						this.$refs.wxPay.innerHTML = "";
						new QRCode(this.$refs.wxPay, {
							text: res.data.data.qrcode,
							width: 200,
							height: 200,
						});
					});
					this.aliPayTimer = setInterval(() => {
						getAliOrderStatus({
							out_trade_no: res.data.data.out_trade_no,
						}).then((result) => {
							if(result.data.code == "403") {
								this.$message({
									type: "warning",
									message: ""
								});
								this.wxDialog = false;
								return;
							}
							if(result.data.code == "200") {
								clearInterval(this.aliPayTimer);
								this.handleBuyFinish(count, res.data.data.out_trade_no);
								return;
							}
						}).catch((e) => {
							console.log(e);
						})
					}, 2000);
				});

				// let sns = this.sn_arr.map((item) => {
				// 	return item.value;
				// });
				// let timer = null;
				// getOrderId({
				// 	cardNo: this.card_num,
				// 	count: +this.active_nums,
				// 	productSeriesId: this.$route.params.id,
				// 	sns,
				// }).then((res) => {
				// 	requestWxPay({
				// 		orderId: res.data.data,
				// 		productSeriesId: this.$route.params.id,
				// 	}).then((result) => {
				// 		timer = setInterval(() => {
				// 			getOrderStatus({
				// 				// orderId: res.data.data
				// 				orderId: res.data.data,
				// 			}).then((result2) => {
				// 				console.log(timer, "timer");
				// 				if (result2.data.data[0].payStatus == 1) {
				// 					clearInterval(timer);
				// 				}
				// 			});
				// 		}, 500);
				// 	});
				// });
			} else if (this.pay_way == "") {
				this.$message({
					type: "warning",
					message: "!",
				});

				// let sns = this.sn_arr.map((item) => {
				// 	return item.value;
				// });
				// let timer = null;
				// requestWxPay({
				// 	cardNo: this.card_num,
				// 	count: +this.active_nums,
				// 	productSeriesId: this.$route.params.id,
				// 	sns,
				// }).then((result) => {
				// 					this.wxDialog = true;
				// 			this.$nextTick(() => {
				// 				this.$refs.wxPay.innerHTML = "";
				// 				new QRCode(this.$refs.wxPay, {
				// 					text: result.data.data.url_code,
				// 					width: 200,
				// 					height: 200,
				// 				});
				// 			});
				// 	timer = setInterval(() => {
				// 		getOrderStatus({
				// 			// orderId: res.data.data
				// 			orderId: '202103250935101616636110013',
				// 		}).then((result2) => {
				// 			console.log(timer, "timer");
				// 			if (result2.data.data[0].payStatus == 1) {
				// 				clearInterval(timer);
				// 				this.handleBuyFinish(result2.data.data.length, '202103250935101616636110013')
				// 			}
				// 		});
				// 	}, 2000);
				// });
			} else {
				this.dialogVisible = true;
			}
		},
		// 
		init() {
			getProductDetail({
				id: this.$route.params.id,
			}).then((res) => {
				let productSeries = res.data.data.productSeries;
				this.product_logo =
					this.baseUrl + "/file/" + productSeries.logo.replace(/.*\//g, "");
				this.tableData[0].chName = productSeries.chName;
				this.tableData[0].enName = productSeries.enName;
				this.tableData[0].introduction = productSeries.introduction;
				this.tableData[0].originalPrice = productSeries.originalPrice;
				this.tableData[0].actualPrice = productSeries.actualPrice;
				this.tableData[0].chName = productSeries.chName;
			});
		},
		// 
		changePayWay(row) {
			this.pay_way = row.name;
		},
		// 
		buyProductByCard() {
			if (this.card_num == "") {
				return;
			}
			let sns = this.sn_arr.map((item) => {
				return item.value;
			});
			cardPay({
				cardNo: this.card_num,
				count: +this.active_nums,
				productSeriesId: this.$route.params.id,
				sns,
			}).then((res) => {
				if (res.data.code == 400) {
					this.$message({
						type: "warning",
						message: res.data.data,
					});
					return;
				}
				if (res.data.code == 401) {
					this.$message({
						type: "warning",
						message: res.data.data,
					});
					this.$router.push("/productList");
					return;
				}
				this.card_num = "";
				this.handleBuyFinish(+this.active_nums, res.data.data);
			});
		},
		// 
		handleBuyFinish(count, orderNo) {
			this.wxDialog = false;
			if (count == 1) {
				getInfoByOrderNo({
					orderId: orderNo,
				}).then((res) => {
					this.dialogVisible = false;
					this.payInfoVisible = true;
					this.buyFinishInfo.productName = this.tableData[0].chName;
					this.buyFinishInfo.snCode = res.data.data[0].sn;
					this.buyFinishInfo.activeCode = res.data.data[0].activationCodeNo;
				});
			} else {
				this.$message({
					type: "success",
					message: "!",
				});
				this.$router.push({
					name: "paySuccess",
					params: {
						id: orderNo,
					},
				});
			}
		},
		// 
		back() {
			window.history.back();
		},
	},
	beforeDestroy() {
		clearInterval(this.aliPayTimer)
	}
};
</script>

<style lang="scss" scoped>
.product-buy-wrapper {
	.product-buy-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid #ddd;
		padding-bottom: 15px;
		.header-left {
			display: flex;
			align-items: center;
			background: url("../../assets/images/ico_navdot.png") no-repeat left 10px;
			.solution-top {
				font-size: 20px;
				line-height: 36px;
				font-family: "";
				padding-left: 23px;
				color: #3b3b3b;
			}
			.back {
				margin-left: 10px;
				width: 35px;
